.envira-welcome-wrap {
	padding: 20px 30px;
	.nav-tab-wrapper {
		a {
			font-weight: 400;
			font-size: 18px;
			line-height: 24px;
		}
		a.nav-tab-active {
			font-weight: 600;
		}
	}
	.nav-tab {
		margin-right: 0.85em;
	}

	.envira-title {
		width: 100%;
		margin-bottom: 20px;

		h1.welcome-header {
			background: none;
			font-size: 42px;
			font-weight: 400;
			padding-left: 0;
			span {
				color: #7cc048;
				margin-right: -10px;
				text-indent: 0;
				line-height: 1;
				font-size: 33px;
			}
		}
		h1.welcome-header,
		.about-text {
			text-indent: 10px;
		}
		.about-text {
			font-size: 21px;
			line-height: 32px;
			font-weight: 200;
		}

	}

	.envira-welcome-sidebar {
		float: right;
		width: 20%;
		padding: 15px;

		.sidebox {
			background: #e6e6e6;
			padding: 20px;
			font-size:  14px;
			margin-bottom: 20px;
			h4 {
				font-weight: 600;
				line-height: 1.3;
				color: #000;
				margin: 0 0 10px 0;
			}
			input[type='password'],
			input[type='text'],
			input[type='email'] {
				width: 100%;
			}
			input[type='submit'],
			input[type='button'] {
				margin: 10px 0 0;
			}
			input[type='checkbox'] {

			}
			.form-row {
				input[type='checkbox'] {
					margin: 2px;
				}
				label {
					margin-left: 5px;
					font-size: 12px;
				}
				margin: 2px 0;
				width: 100%;
				font-size: 14px;
				line-break: 14px;
			}
			&.warning {
				background: #950303;
				color: white;
				h4 {
					color: #ffae00;
				}
				p {
					strong {
						color: #ffae00;
					}
				}
			}
		}
		sidebox:last-of-type {
			margin-bottom: 0;
		}
	}

	.envira-welcome-main,
	.envira-get-started-main,
	.envira-support-main {
		width: 75%;
		float: left;
	}

	.envira-wrap {
		padding: 20px;
	}

}

.changelog-main {
	padding: 0 10px;
	ul {
		list-style: disc;
		list-style-position: inside;
	}
}

/*------------------------------------------------------------------------------
* 1.0 Welcome Screen
*----------------------------------------------------------------------------*/

/*
* 1.1 Version Badge
*/
.envirabadge {
	color: #d84800;
	display: inline-block;
	font: 400 150px/1 dashicons !important;
}

.envirabadge:before {
	content: "\f448";
}

.about-wrap .envirabadge {
	position: absolute;
	top: 0;
	right: 0;
}

.index_page_enviracredits code,
.index_page_enviraabout code {
	background-color: #e0e0e0;
	color: #636363;
	font-size: 1em;
}

@media only screen and (max-width: 500px) {

	.about-wrap .envirabadge {
		position: relative;
		margin: 10px auto;
		top: auto;
		right: auto;
	}
}

