/*------------------------------------------------------------------------------
* 3.0 Upgrade Screen
*----------------------------------------------------------------------------*/

.upgrade-wrap {
	text-align: center;
	h3 {
		font-size: 28px;
		line-height: 28px;
		font-weight: 400;
		margin: 35px auto 10px auto;
	}
	h4.headline-subtitle {
		font-size: 20px;
		line-height: 24px;
		font-weight: 200;
		margin: 20px auto;
		display: block;
	}
	a.button {
		margin: 10px auto;
		display: table;
		clear: both;
	}
}
.upgrade-list {
	display: table;
	margin-bottom: 40px;
	border-bottom: 1px solid #ccc;
	padding-bottom: 35px;
	ul {
		width: 85%;
		margin: 20px auto;
	}
	li {
		width: 50%;
		margin: 20px auto;
		float: left;
		display: block;
		font-size: 16px;
		line-height: 18px;
		min-height: 75px;
		.interior {
			padding: 0 10px;
		}
		h5 {
			font-size: 18px;
			line-height: 24px;
			margin: 5px auto;
			font-weight: 700;
		}
		p {
			margin: 0;
		}
	}
}
.upgrade-video {
	position: relative;
	padding-bottom: 56.25%; /* 16:9 */
	padding-top: 25px;
	height: 0;
}
.upgrade-video iframe {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
}

