/**
* ACF Template: Pricing Table
*/
section.pricing-table {
	padding-bottom: 0;

	ul, ol {
		list-style: none;
	}

	/**
	* Packages
	*/
	ul.packages {
		clear: both;
		float: left;
		width: 100%;
		border: 1px solid #ccc;
		li.popular {
			background-color: rgb(243, 249, 255);
		}

		li.package {
			float: left;
			width: 25%;
			border-right: 1px solid #ccc;
			&.package-wide {
				width: 20%;
			}
			&:last-child {
				border-right: none;
			}

			/**
			* Most Popular
			*/
			span.most-popular {
				display: block;
				float: left;
				width: 100%;
				height: 36px;
				line-height: 36px;
				margin: -36px 0 0 0;
				text-align: center;
				text-transform: uppercase;
				color: #FFF;
				background: #3e3e3e;
				font-size: 15px;
				border: 1px solid #ccc;
				border-bottom: none;
			}

			ul {
				li {
					clear: both;
					float: left;
					width: 100%;
				}
				li.title {
					padding: 30px 40px 0 40px;
					text-align: center;

					font-size: 24px;
					line-height: 24px;
				}
				li.price {
					padding: 20px 33px 0 33px;
					text-align: center;

					font-size: 60px;
					line-height: 60px;
					color: #3e3e3e;
					.slash-price {
						display: block;
						line-height: 20px;
						font-size: 16px;
						text-decoration: line-through;
					}
					.time {
						font-size: 14px;
						color: rgb(180, 180, 180);
						margin-left: -12px;
					}
					sup {
						display: inline-block;
						margin: 0 -12px 0 0;
						padding: 0;
						font-size: 24px;
					}
					span {
						display: inline-block;
					}
					sub {
						position: relative;
						display: inline-block;
						vertical-align: baseline;
						line-height: 0;
						margin: 0 0 0 -12px;
						bottom: -0.25em;
						font-size: (14 - 3);
					}
				}
				li.description {
					padding: 30px 30px 0 30px;
					text-align: center;

					font-size: 16px;
					line-height: 24px;
				}
				li.cta {
					padding: 20px 30px 0 30px;
					text-align: center;

					a.button {
						float: left;
						width: 100%;
						padding-left: 10px;
						padding-right: 10px;
						font-size: 18px;
						text-transform: uppercase;

					}
				}
				li.list {
					padding: 30px 25px 0 25px;

					hr {
						margin: 0 0 30px 0;
					}
				}
			}

			/**
			* Disabled Packages
			* - User cannot upgrade to this package, as they already have it / have a higher package
			*/
			&.disabled {
				/**
				* Ensures "Most Popular" isn't greyed out, as for upgrades we use this to flag
				* the Current Plan the user is on
				*/
				> ul {
					opacity: 0.2;
				}
			}
		}
	}
}

/**
* ACF Template: Pricing Table Comparison
*/
body.envira_page_envira-gallery-upgrade section.pricing-table-comparison {

	clear: both;
	float: left;
	width: 100%;
	padding: 60px 0;
	background: #fff;
	margin-bottom: 30px;

	.container {
		width: 90% !important;
		margin: 0 auto;
	}

	ul, ol {
		list-style: none !important;
	}
	li, dd {
		margin: 0;
	}

	/**
	* Packages
	*/
	ul.packages {
		clear: both;
		float: left;
		width: 100%;
		border-bottom: 1px solid #ccc;

		/**
		* Rows
		*/
		li.row {
			clear: both;
			float: left;
			width: 100%;
			display: flex;
			flex-direction: row;
			flex-wrap: wrap;

			ul {
				clear: both;
				float: left;
				width: 100%;
				border-left: 1px solid #ccc;
				border-right: 1px solid #ccc;

				li {
					float: left;
					width: 24.94%;
					// padding: 20px 10px;
					border-top: 1px solid #ccc;
					border-right: 1px solid #ccc;
					text-align: center;
					font-size: 14px;
					line-height: 52px;

					&.feature {
						width: 24.94%;
						text-align: left;
						text-indent: 30px;
						strong {
							line-height: 42px;
						}



						a.tooltip {
							display: none;
							float: right;
							width: 18px;
							height: 18px;
							background: url(../images/icons/question.svg) center
								no-repeat;
							background-size: 18px 18px;
							text-indent: -9999px;
						}
					}

					&.yes {
						text-indent: -9999px;
						background: url(../images/icons/tick.svg) center no-repeat;
						background-size: 20px 20px;
					}

					&.no {
						text-indent: -9999px;
						background: url(../images/icons/cross.svg) center no-repeat;
						background-size: 20px 20px;
					}

					&.popular {
						background-color: #f1f9ff;
					}

					&:last-child {
						border-right: none;
					}

					/**
					* Call to Action Buttons
					* - Appear in header and footer, so CSS is here
					*/
					a.button {
						float: left;
						width: 100%;
						padding: 0;
						line-height: 38px;
						font-size: 14px;
						text-transform: uppercase;


						&.desktop {
							display: block;
						}
						&.mobile {
							display: none;
						}
					}
				}
			}
		}
		&.packages-wide {
			li.row {
				ul {
					li {
						width: 16%;
						&.feature {
							width: 20%;
						}
						&.title,
						&.price,
						&.cta {
							width: 100%;
						}
					}
				}
			}
		}
		/**
		* Header
		*/
		li.header {
			ul {
				border-left: none;

				li {
					padding: 0;
					border-right: none;

					&.blank {
						border: none;
					}

					&:nth-child(2) {
						ul {
							border-left: 1px solid #ccc;
						}
					}

					&:last-child {
						ul {
							border-right: none;
						}
					}

					/**
					* Most Popular
					*/
					span.most-popular {
						display: block;
						float: left;
						width: 100%;
						height: 36px;
						line-height: 36px;
						margin: -36px 0 0 0;
						text-align: center;
						text-transform: uppercase;
						color: #FFF;
						background: #3e3e3e;
						font-size: 14px;
						border: 1px solid #ccc;
						border-bottom: none;
					}

					ul {
						li {
							width: 100%;
							border: none;
						}
						li.title {
							padding: 30px 0px 10px 0px;
							text-align: center;

							font-size: 24px;
							line-height: 24px;
						}
						li.price {
							padding: 0 0px 25px 0px;
							text-align: center;

							font-size: 56px;
							line-height: 56px;
							color: #3e3e3e;
							.slash-price {
								display: block;
								line-height: 20px;
								font-size: 14px;
								text-decoration: line-through;
							}
							sup {
								display: inline-block;
								margin: 0 -12px 0 0;
								padding: 0;
								font-size: 24px;
							}
							span {
								display: inline-block;
							}
						}
						li.cta,
						li.bottom-cta {
							// padding: 0 10px 15px 10px;
							text-align: center;
							border: none;
							a {
								width: 90%;
								display: table;
								margin: 20px auto;
								float: none;
							}
						}
					}
				}
			}
		}

		li.bottom-cta {
			// padding: 0 10px 15px 10px;
			text-align: center;
			border: none;
			a {
				width: 90% !important;
				display: table !important;;
				margin: 20px auto !important;;
				float: none !important;;
			}
		}

		/**
		* Sub Header
		*/
		li.sub-header {
			border-left: 1px solid #ccc;
			border-top: 1px solid #ccc;
			border-right: 1px solid #ccc;
			font-size: 19px;
			font-weight: 600;
			padding: 15px 0px;
			text-indent: 20px;
			background: #eee;
			width: -webkit-calc(100% - 2px);
			width: -moz-calc(100% - 2px);
			height: -o-calc(100% - 2px);
			width: calc(100% - 2px);
		}

		/**
		* Disabled Packages
		* - User cannot upgrade to this package, as they already have it / have a higher package
		*/
		li.disabled {
			/**
			* Ensures "Most Popular" isn't greyed out, as for upgrades we use this to flag
			* the Current Plan the user is on
			*/
			opacity: 0.2;
		}
	}

}

body.envira-upgrade-page.envira-license-basic {
	td.plus,
	li.plus {
		display: none;
	}
}

body.envira-upgrade-page.envira-license-plus {
	td.basic,
	li.basic {
		display: none;
	}
