/*------------------------------------------------------------------------------
* 1.0 Media Queries
*----------------------------------------------------------------------------*/

@media screen and ( max-width: 1750px ) {
	body.envira_page_envira-gallery-upgrade section.pricing-table-comparison {
		/**
		* Packages
		*/
		ul.packages {
			li.row {
				ul {
					li {
						width: 33.3%;
					}
					li.feature span {
						height: 15px;
						display: block;
					}
					li.feature {
						padding: 0 20px;
						line-height: 15px;
						text-indent: 0;
						width: -webkit-calc(33.3% - 42px);
						width: -moz-calc(33.3% - 42px);
						height: -o-calc(33.3% - 42px);
						width: calc(33.3% - 42px);
					}
				}
			}
		}
	}
}

